.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.jbinput {
    width: 200px;
    height: 40px;
    border-width: 1px;
    border-color: lightgray;
    border-style: solid;
    border-radius: 10px;
    margin-left: 5px;
    margin-right: 5px;
    text-align: center;
}


.msubmit {
      width: 235px;
      height: 61px;
      border-radius: 30.5px;
      background-color: #66cccc;
      border: none;
      font-size: 10px;
      font-weight: bold;
      font-family: 'Montserrat', sans-serif;
      line-height: 4.75;
      letter-spacing: 2.9px;
      text-align: center;
      color:white;
      margin: auto;
      left: 0;
      top: 0;
      right: 0;
      bottom: 0;
      display: block;
      -webkit-transition: 0.3s;
      -o-transition: 0.3s;
      transition: 0.3s;
}

.msubmit:hover {
    cursor: pointer;
    border-style: solid;
    border-width: 3px;
    border-radius: 0px;
    border-color: #66cccc;
    color: #66cccc;
    background-color: white;
}

.jbresult{
    padding: 30px;
    font-size: 15pt;
    color: #9fa3a7;
    font-family: 'Montserrat', sans-serif;
}

.jbresultlink{
    padding: 30px;
    font-size: 15pt;
    color: #66cccc;
    font-family: 'Montserrat', sans-serif;
}

.jbhead{
    padding: 30px;
    font-size: 12pt;
    color: black;
    font-family: 'Montserrat', sans-serif;
}

.delet {
    background-color: #f5f5f5;
    border: none;
    font-size: 20px;
    font-weight: bold;
    font-family: 'Montserrat', sans-serif;
    color: firebrick;
}

.graybg {
    width: 75%;
    height: auto;
    background-color: #f5f5f5;
    z-index: -1;
}

.mimg {
     vertical-align:middle;
}

.seventy {
    width: 75%;
    margin-top: 20px;
    margin-bottom: 20px;
}

.title {
    padding: 30px;
    font-size: 15pt;
    color: #212121;
    font-family: 'Montserrat', sans-serif;
}


